import { Component, Vue, Prop } from 'vue-property-decorator'
import treeToArray from './eval'
import { Column } from '@/domains/column'
import { EvalMenuList } from '@/utils/evalData'
import { Menu } from '@/domains/menu'

@Component({
  name: 'TreeTable'
})
export default class TreeTable extends Vue {
  @Prop({ required: true })
  private data!: any[]
  @Prop({ required: true })
  private columns!: Column[]
  @Prop({ default: false })
  private single!: boolean
  @Prop()
  private evalFunc!: any
  @Prop()
  private evalArgs!: any[]
  @Prop({ default: false })
  private expandAll!: boolean

  /** 格式化数据源 */
  get formatData() {
    let tmp
    if (!Array.isArray(this.data)) {
      tmp = [this.data]
    } else {
      tmp = this.data
    }
    const func = this.evalFunc || treeToArray
    // const args = this.evalArgs ? Array.concat([tmp, this.expandAll], this.evalArgs) : [tmp, this.expandAll]
    const args = this.evalArgs ? [tmp, this.expandAll].concat(this.evalArgs) : [tmp, this.expandAll]
    return func.apply(null, args)
  }
  private showRow(row: any): string {
    const show = (row.row.parent ? (row.row.parent._expanded && row.row.parent._show) : true)
    row.row._show = show
    return show ? 'animation' : 'hidden'
    // return show ? 'animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;' : 'color: red; display: "none"'
  }
  // 切换下级是否展开
  private toggleExpanded(trIndex: number) {
    const record = this.formatData[trIndex]
    record._expanded = !record._expanded
  }
  // 图标显示
  private iconShow(index: number, record: Menu) {
    return (index === 0 && record.list && record.list.length > 0)
  }
  private handleSelectionChange(val: any) {
    this.$emit('handleSelectionChange', val)
  }
  private select(selection: any, row: any) {
    if (this.single && selection.length > 1) {
      selection.shift()
    }
  }
}
