



































































import { Component, Vue } from 'vue-property-decorator'
import TreeTable from '@/components/TreeTable/index.vue'
import Edit from './edit.vue'
import { fetchList, deleteData } from '@/api/sysManage/menuManage'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Menu } from '@/domains/menu'
import { checkSelection } from '@/utils/checkSelection'
import { EvalMenuList } from '@/utils/evalData'

class Query extends ListQuery {
  public menuName: string = ''
}

@Component({
  name: 'MenuManage',
  components: {
    TreeTable,
    Edit
  },
  filters: {
    statusFilter(type: number) {
      const types = ['目录', '菜单', '按钮']
      return types[type]
    }
  }
})

export default class MenuManage extends Vue {
  /** 是否显示table列表 */
  private isShowList: boolean = true
  /** 查询条件 */
  private listQuery: Query = new Query()
  /** 数据列表 */
  private list: Menu[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: '菜单ID',
      key: 'menuId'
    },
    {
      label: '菜单名称',
      key: 'name'
    },
    {
      label: '上级菜单',
      key: 'parentName'
    },
    {
      label: '图标',
      key: 'icon'
    },
    {
      label: '类型',
      key: 'type',
      slot: 'type'
    },
    {
      label: '排序号',
      key: 'orderNum'
    },
    {
      label: '菜单URL',
      key: 'url'
    },
    {
      label: '权限标识',
      key: 'perms'
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前编辑数据的ID */
  private id: string = '0'
  /** 当前选中的数据 */
  private multipleSelection: Menu[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'menuId'
  /** tab标题，编辑或者新增 */
  get tabLabel() {
    return this.type === 'edit' ? this.$t('common.edit') : this.$t('common.create')
  }

  private created(): void {
    this.getList()
  }

  /** 获取数据列表 */
  private getList(): void {
    fetchList(this.listQuery).then((response: any) => {
      this.list = EvalMenuList(response.menuList)
    })
  }
  /** 搜索 */
  private handleFilter(): void {
    this.listQuery._search = true
    this.listQuery.page = 1
    this.getList()
  }
  /** 新增 */
  private handleCreate(): void {
    this.type = 'save'
    this.isShowList = false
  }
  /** 编辑 */
  private handleEdit(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.id = this.multipleSelection[0][this.idKey] as string
      this.type = 'edit'
      this.isShowList = false
    })
  }
  /** 删除数据 */
  private handleDelete(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.$confirm('是否确定删除该数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids: string[] = []
        this.multipleSelection.map(item => {
          ids.push(item[this.idKey] as string)
        })
        deleteData(ids).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.handleFilter()
        })
      }).catch(() => {})
    })
  }
  private handleSelectionChange(val: Menu[]): void {
    this.multipleSelection = val
  }
  /** 监听子组件，执行方法 */
  private getMsgFromChild(v: string): void {
    if (v !== 'cancel') {
      this.handleFilter()
    }
    this.isShowList = true
  }
  /** 去到icon的路由，查看icon */
  private handleIcon(): void {
    this.$router.push('/example/icon')
  }
}
