import { Menu } from '@/domains/menu'

export function EvalMenuList(data: Menu[]): Menu[] {
  const result: Menu[] = []

  data.map((item: Menu) => {
    if (item.parentId === '0') {
      result.push(item)
    }
  })

  for (let i = 0; i < result.length; i++) {
    const parent = result[i]
    parent.list = []
    data.map(item => {
      if (item.parentId === parent.menuId) {
        parent.list.push(item)
      }
    })
    for (let j = 0, length = parent.list.length; j < length; j++) {
      parent.list[j].list = []
      data.map(item => {
        if (item.parentId === parent.list[j].menuId) {
          parent.list[j].list.push(item)
        }
      })
      for (let k = 0, length = parent.list[j].list.length; k < length; k++) {
        parent.list[j].list[k].list = []
        data.map(item => {
          if (item.parentId === parent.list[j].list[k].menuId) {
            parent.list[j].list[k].list.push(item)
          }
        })
      }
    }
  }
  return result
}
