import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { Menu } from '@/domains/menu'

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/menu/list',
    method: 'get',
    params: query
  })
}

export function fetchEditInfo(id: string) {
  const url = '/sys/menu/info/' + id
  return request({
    url,
    method: 'get'
  })
}

export function fetchMenuSelect() {
  return request({
    url: '/sys/menu/select',
    method: 'get'
  })
}

export function saveData(data: Menu) {
  return request({
    url: '/sys/menu/save',
    method: 'post',
    data
  })
}

export function updateData(data: Menu) {
  return request({
    url: '/sys/menu/update',
    method: 'put',
    data
  })
}

export function deleteData(id: string[]) {
  const url = '/sys/menu/delete/' + id
  return request({
    url,
    method: 'delete'
  })
}
