



















































































import { fetchMenuSelect, saveData, fetchEditInfo, updateData } from '@/api/sysManage/menuManage'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { Menu } from '@/domains/menu'
import { EvalMenuList } from '@/utils/evalData'
import { Tree } from 'element-ui'

@Component({
  name: 'MenuEdit'
})
export default class Edit extends Vue {
  @Prop()
  /** 编辑记录ID */
  private id!: string
  /** 状态，save或edit */
  @Prop({ default: 'save' })
  private type!: string
  /** form数据 */
  private ruleForm: Menu = new Menu()
  /** form校验 */
  private rules: any = {
    name: [
      { required: true, message: '请输入菜单名称', trigger: 'blur' }
    ],
    url: [
      { required: true, message: '请输入菜单url', trigger: 'blur' }
    ]
  }
  /** tree显示字段 */
  private defaultProps: any = {
    children: 'list',
    label: 'name'
  }

  created() {
    this.ruleForm.parentId = '0'
    this.ruleForm.parentName = '一级菜单'
    if (this.type === 'edit') {
      this.getEditInfo()
    }
    this.getMenuList()
  }
  /** 是否显示选择父级菜单 */
  private isShowDialog: boolean = false
  /** 当前选中的父级menuItem */
  private selectMenu: Menu = new Menu()
  /** 提供选择父级的menuList */
  private selectList: any[] = []
  // 校验提交
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 保存
  private handleSubmit() {
    saveData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 修改
  private handleEdit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 取消
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 获取adminlist列表
  private getMenuList() {
    fetchMenuSelect().then((response: any) => {
      const menuList = EvalMenuList(response.menuList)
      this.selectList = [
        {
          name: '一级菜单',
          menuId: 0,
          list: menuList
        }
      ]
      if (this.type === 'edit') {
        this.getEditInfo()
      }
    })
  }
  // 获取编辑信息
  private getEditInfo(): void {
    fetchEditInfo(this.id).then((response: any) => {
      const data: Menu = response.menu
      this.ruleForm = Menu.build(data)
      // 因为response没有parentName，只能通过parentId去遍历获取parentName
      for (let i = 0; i < this.selectList.length; i++) {
        if (this.selectList[i].menuId === this.ruleForm.parentId) {
          this.ruleForm.parentName = this.selectList[i].name
          break
        }
        for (let j = 0, length = this.selectList[i].list.length; j < length; j++) {
          const item = this.selectList[i].list[j]
          if (item.menuId === this.ruleForm.parentId) {
            this.ruleForm.parentName = item.name
            break
          }
          for (let k = 0, length = item.list.length; k < length; k++) {
            const child = item.list[k]
            if (child.menuId === this.ruleForm.parentId) {
              this.ruleForm.parentName = child.name
              break
            }
          }
        }
      }
    }).catch(err => {
      console.log(err)
    })
  }
  // 选择菜单节点时，先保存item
  private handleNodeClick(item: Menu) {
    this.selectMenu = item
  }
  // 确定选择菜单
  private handleChange() {
    this.ruleForm.parentName = this.selectMenu.name
    this.ruleForm.parentId = this.selectMenu.menuId
    this.isShowDialog = false
  }
}
